@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.navbar {
    background-color: #333;
    overflow: hidden;
    font-size: 20px;
}

.navbar-container {
    margin: 0;
}

.navbar-name {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 0px 1000px 0px 0px;
    text-decoration: none;
}

.nav-list {
    list-style-type: none;
    margin: 0;
    width: 2000px;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

.nav-item {
    display: inline-block;
    padding: 15px 25px;
    font-family: "Open Sans", sans-serif;
}

.nav-links {
    color: white;
    text-align: center;
    padding: 15px 20px;
    text-decoration: none;
}

.nav-links:hover {
    background-color: #111;
}

/* Defaults */
body {
    background-color: #212930;
    font-family: "Open Sans", sans-serif;
    color: white;
}

.home-container {
    padding: 60px 40px 40px;
}

.home-image {
    display: inline-block;
    vertical-align: top;
    width: 46%;
    margin: 50px 0px 0px 100px;
}

.home-text {
    display: inline-block;
    width: 46%;
    margin: 200px 0px 0px 0px;
    font-size: 25px;
}



.aboutme-header {
    font-size: 40px;
}

.aboutme-text {
    width: 50%;
    font-size: 20px;
}

